@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.city-search-form {
    width: 35%;
    margin: 1% 0 0 0;
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    justify-content:center;
    @include mobile-only {
        width: 100%;
    }
    &__auto-suggest {
        width:50%;
        height:0;
        overflow-y:visible;
        z-index:99;
        @include mobile-only {
            width: 100%;
        }
        
        &__city {
            text-align:center;
            background-color:rgba(255, 255, 255, 0.952);
            border: 1px solid rgba(0, 0, 0, 0.349);
            font-weight:bold;
            color: #1163af;
            padding: 1vw;
            @include mobile-only {
                padding: 2vh;
            }
            &:hover {
                background-color: rgba(255, 203, 105, 0.972);
            }
        }
    }

}