@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";


.detailed-forecast {
    width: 40vw;
    justify-content:center;
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    height: max-content;
    border-radius: 0.3vw;
    height: 100%;


    @include mobile-only {
        width: 100%;
        background-color: rgba(0, 5, 26, 0.863);
        z-index:98;
        position: -webkit-sticky;
        position: sticky;
        top: 7vh;
        left: 0px;
        right:0px;
    }
    &__message-container {
        width:80%;
        height: 40vh;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.5vw;
        color:rgb(0, 114, 167);
        @include mobile-only {
            height:max-content;
            font-size: 2.5vh;
        }
    }
    &__data-container {
        display:flex;
        flex-direction:row;
        align-items: center;
        justify-content:center;
        overflow:hidden;
        font-size:0.9vw;
        color:rgb(0, 114, 167);
        width:100%;
        height: 100%;
        
        @include mobile-only {
            margin:0;
            font-size: 2vh;
            width: 100%;
            box-shadow:none;
            border-bottom: 1px solid rgb(255, 255, 255);
            color:rgb(235, 249, 255);
        }
        &--header {
            background-color: rgba(0, 0, 0, 0.7);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 68% 100%, 0 0);
            justify-content:flex-end;
            @include mobile-only {
                clip-path:unset;
                font-size: 2vh;

            }
        }
    }
    &__time {
        font-size: 1.1vw;
        width: 20%;
        padding: 1vw;
        color: white;
        @include mobile-only {
            font-size: 2vh;
            width: 60vw;
            min-height: 0;
            padding: 0;
        }
    }
    &__weather-icon {

        width: 20%;
        font-size: 1.2vw;
        padding-top: 1vh;
        @include mobile-only {
            font-size: 3vh;
        }
    }
    &__data {
        width: 50vw;

        text-align:left;
        padding: 0.3vw;
        font-weight: bold;
        color: rgb(3, 123, 236);

        white-space:nowrap;
        @include mobile-only {
            text-shadow: 0 0 2px rgb(0, 0, 0);
        }


        
    }
    &__data-label {
        width: 50vw;
        text-align:right;
        height: 100%;
        padding: 0.5vw;
        border-right: 1px solid orangered;
        color:rgb(0, 114, 167);
        box-sizing:border-box;
        @include mobile-only {
            background-color: transparent;
            color:white;
        }

    }
}