@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.forecast {
    &__root-container {
        background-color:transparent;
        display:flex;
        flex-direction:row;
        flex-wrap:nowrap;
        align-items:flex-start;
        background:rgba(218, 218, 218, 0.25);
        justify-content: space-between;
        @include mobile-only {
            flex-direction:column-reverse;
            flex-wrap:wrap;
            justify-content: flex-start;
            align-items:flex-start;
        }
    }
    &__cards-container {
        display: flex;
        flex-direction:row;
        align-items:center;

    }
    &__card {
        display:flex;
        flex-basis: 25%;
        flex-direction: row;
        box-sizing: border-box;
        overflow:hidden;
        color:rgb(0, 114, 167);
        background:white;
        height:50%;
        @include mobile-only {
            flex-basis: 50%;
            width:50%;
            height: auto;
            font-size: 2vh;
            flex-direction:column;
            flex-wrap:nowrap;
            box-shadow:unset;
            overflow:hidden;
            border-right: 2px solid rgba(0, 113, 131, 0.692);
            margin-bottom: 1vh;
            border-bottom:2px solid rgba(0, 134, 155, 0.685);
            background: rgb(0,213,244);
            background: linear-gradient(139deg, rgba(0,213,244,0.33655469023546913) 0%, rgba(169,202,255,0.6390756986388305) 18%, rgba(255,255,255,0.6390756986388305) 47%);
            &:nth-child(even) {
                background: rgb(0,213,244);
                background: linear-gradient(207deg, rgba(0,213,244,0.33655469023546913) 0%, rgba(169,202,255,0.6390756986388305) 18%, rgba(255,255,255,0.6390756986388305) 47%);
            }
        }
        &:nth-child(1){
            background-color: rgba(238, 238, 238, 0.43);
        }
        &:nth-child(3){
            background-color: rgba(238, 238, 238, 0.43);
        }
        &:nth-child(6){
            background-color: rgba(238, 238, 238, 0.43);
        }
        &:nth-child(8){
            background-color: rgba(238, 238, 238, 0.43);
        }
        &__data-container {
            width: 60%;
            font-size:0.9vw;
            display:flex;
            flex-direction: row;
            flex-wrap:wrap;
            justify-content: flex-start;
            font-weight:bold;

            @include mobile-only {
                flex-direction:row;
                width: 100%;
                flex-wrap:wrap;
                background-color: rgba(255, 255, 255, 0);
            }
            
        }

        &--current {
            width: 100%;
            color:white;
            background-color: transparent;
            overflow:hidden;
            height: max-content;
            flex-basis: 75%;
            box-shadow:none;
            
            @include mobile-only {
                flex-basis: 98%;
                width:100%;
                height: auto;
                margin:0; 
                background:unset;
                align-items:center;
                background:url("../images/stripes-light-rev.png")

            }
            &:nth-child(1){
                background-color: rgba(255, 255, 255, 0);
            }

        }
        &--selected {
    

        }
        &__city-info-container {
            font-size: 2vw;
            text-align: center;
            width: max-content;
            display:flex;
            flex-direction:row;
            flex-wrap:wrap;
            align-items:center;
            text-align:left;
            padding-left: 1vw;
            @include mobile-only {
                font-size: 2.5vh;
                width:max-content;
                position:absolute;
                top: 4.5vh;
                left: 1%;

                width:99%;

            }
           
        }
        &__city {
            font-size: 2vw;
            width:100%;
            @include mobile-only {
                font-size: 2vh;
            }
        }
        &__country {
            font-size: 2vw;
            width:100%;
            @include mobile-only {
                font-size: 2vh;
            }
        }

        &__parameters-container {
            display:flex;
            flex-direction:row;
            justify-content: space-evenly;
            & > * {
                font-size: 1vw;
            }
            @include mobile-only {
                justify-content: space-around;
            }
        }
        
        &__time-weather {
            display:flex;
            flex-direction:column;
            align-items:center;
            flex-wrap:nowrap;
            justify-content: flex-start;
            width: 40%;

            background:rgba(2, 63, 119, 0.64);
            padding: 1vh 0;         
            @include mobile-only {
                height: auto;
                align-items:flex-start;
                width:100%;
                border:none;
                flex-direction:row;
                justify-content:space-between;

                padding: 1vh 1vw;;

            }
            &--current {
                width:15%;
                @include mobile-only {
                    width:100%;
                }
            }
            &--selected {
                background-color:orange;
            }
            &__time {

                color:white;
                text-align:left;
                /*background-color:rgba(0, 0, 0, 0.603);*/
                text-align:center;
                font-size: 2vw;
                @include mobile-only {
                    font-size: 3vh;
                    margin-right:-5%;
                    z-index: 0;
                    text-align:left;
                    padding-left:1%;
                    height: auto;
                    box-sizing:border-box;
                    width: 30%;
                    color: rgb(255, 255, 255);
                    text-shadow:1px 1px 1px rgb(0, 0, 0)
                }

            }
            &__error {
                font-size: 2vw;
                margin-right: 0;
                color:white;
                @include mobile-only {
                    font-size: 2.5vh;
                    margin-right:-5%;
                    z-index: 99;
                    color:rgb(255, 255, 255);
                    text-align:center;
                    padding: 0;
                    height: auto;
                    box-sizing:border-box;
                    text-shadow:2px 2px 2px rgb(148, 67, 1);
                    background-color: rgba(0, 0, 0, 0.603);
                    padding: 1vh;

                }

            }

            
            &__icon {
                font-size: 3.0vw;
                color:rgba(223, 223, 223, 0.192);
                margin-bottom:1vh;
                z-index:1;

                @include mobile-only {
                    font-size: 5vh; 
                    text-align:right;
                    width: 40%;
                    padding-right:5%;
                   

                }
                &--Rain {
                    background: linear-gradient(to bottom, rgb(255, 255, 255), rgb(167, 216, 255));
                    color: transparent;
                    background-clip: text;  
                }
                &--Clouds {
                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(154, 168, 179));
                    color: transparent;
                    background-clip: text;  
                }
                &--SunClouds {
                    background: linear-gradient(300deg, rgb(255, 255, 255) 30%, rgb(255, 217, 1));
                    color: transparent;
                    background-clip: text;  
                }
                &--Mist {
                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(57, 79, 97));
                    color: transparent;
                    background-clip: text;  
                }
                &--Clear {
                    background: linear-gradient(120deg, rgb(251, 255, 0), rgb(255, 196, 0));
                    color: transparent;
                    background-clip: text;  
                }
                &--Thunderstorm {
                    color: rgb(119, 84, 76);
                }
                &--Snow {
                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(180, 231, 255));
                    color: transparent;
                    background-clip: text;  
                }
                &--big {
                    font-size: 3vw;


                    @include mobile-only {
                        font-size: 7vh;
                        height: auto;
                        margin-top:0;
                        text-align:right;
                        
                    }
                }
            }
        }

        &__parameter {
            display:flex;
            align-items:center;
            width:100%;
            @include mobile-only {
                width: 50%;
                flex-direction:row;
                position: relative;
                &--wind-direction {
                    width:100%;
                    background-color:lightgrey;
                }
                &--temp {
                    width:35%;
                }
                &--wind {
                    width:65%;
                }

            }
            &--header {
                width: 50%;
                font-size: 1.1vw;
                @include mobile-only {
                    width: 50%;
                    flex-direction:row;
                    color:rgb(0, 114, 167);
                    font-size:1.5vh;
                }
            }
            &__icon {

                width: 22%;
                height: 100%;
                margin-right: 0.5vw; 
                text-align:left;
                height: 100%;
                display:flex;
                flex-direction:row;
                align-items:center;
                justify-content:center;
                @include mobile-only {
                    font-size: 2.5vh;
                    height: 4vh;
                    width: 4vh;


                }
                &--temp {
                    color:red;  
                }
                &--wind {
                    color:rgb(0, 114, 167);
                }
                &--wind-direction {
                    color: rgb(55, 195, 0);
                }
                &--cloud-coverage {
                    color:rgb(93, 157, 177);
                }
                &--rain-amount {
                    color:rgb(0, 114, 167);
                }
            }
            &__value {

                width:80%;
                text-align:left;
                @include mobile-only {
                    font-size: 1.7vh;
                    text-align:center;
                }
                &:after {

                    margin-left: 0.2vw;

                }
            }
        }











