    &__day-container {
        display:flex;
        align-items:center;
        justify-content:center;
        max-width: 100%;
        margin: 0;
        & > * {
            justify-content:flex-start;
        }
        &__date {
            font-size: 1.5vw;
            width: 10vw;
        }

    }
    &__day-forecasts {
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        width:60vw;
        /*padding: 0 10%;*/
        justify-content:space-around;
        align-items:center;
        overflow:hidden;
        height: 50vh;
        padding-left: 5vw;
        @include mobile-only {
            justify-content:flex-start;
            width:100%;
            height: max-content;
            padding-left: 0vw;
        }

    }
    
